import { Link } from "gatsby";
import React from "react";

const NavLink = ({ path, link }: { path: string; link: string }) => {
  const linkPath = `/${link.toLowerCase().split(" ").join("-")}`;
  const isCurrent = path.includes(linkPath);

  return (
    <li className="mr-6">
      <Link
        to={linkPath}
        className={`text-xl font-bold text-main-100 hover:text-main-10 ${
          isCurrent && "underline"
        }`}
      >
        {link.toUpperCase()}
      </Link>
    </li>
  );
};

export default NavLink;
